




body, html, *{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body{
  min-height: 100vh;
}
.wrapper{
  composes: wrapper from './App.module.css'
}
.main{
  composes: main from './components/Content.module.css'
}

*::after, *::before{
  box-sizing: border-box;
}

a{
  text-decoration: none;
  color: inherit;
}
ul{
  list-style: none;
}

input{
  outline: none;
}