.displayFlexSpaceBetw {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.effectHover {
	position: relative;

	&::before {
		position: absolute;
		content: '';
		bottom: -5px;
		width: 0;
		height: 2px;
		background-color: #969696;
		transition: all 0.4s;
	}

	&:hover {
		&::before {
			right: 0;
			width: 100%;
			transition: all 0.4s;
		}
	}
}

.header {
	width: 1920px;
	margin: 0 auto;
	background-color: #000;
	z-index: 10;
}

.navTop {
	padding: 14px 0;
	background-color: #000;
}
.navTopInner {
	composes: pt-regular from '../App.module.css';
	composes: displayFlexSpaceBetw;
	color: #969696;
	font-size: 12px;
}

.groupNumber {
	composes: displayFlexSpaceBetw;
	width: 386px;
}

.underLine {
	display: block;
	rotate: 90deg;
	width: 12px;
	height: 1px;
	background-color: #969696;
}

.tel {
	composes: effectHover;
}

.logIn {
	composes: displayFlexSpaceBetw;
	composes: effectHover;

	width: 131px;
}

.nav {
	position: sticky;
	top: 0;
	width: 100%;
	padding: 15px 0;
	background-color: #1f1f1f;
	z-index: 20;
	transition: all 0.4s;
}

.navWrappet {
	composes: displayFlexSpaceBetw;
	color: #969696;
}

.navInner {
	composes: displayFlexSpaceBetw;
	width: 537px;
}

.list {
	position: relative;
	composes: displayFlexSpaceBetw;
	composes: pt-regular from '../App.module.css';
	width: 374px;
	font-size: 14px;
	text-transform: uppercase;
}

.logo {
	composes: black-ops-regular from '../App.module.css';
	cursor: pointer;
	color: #fff;
	font-size: 32px;
}

.listItem {
	cursor: pointer;
	transition: all 0.4s;
}

.listItemActive {
	cursor: pointer;
	color: #fff;
	transition: all 0.4s;
}

.groupSvg {
	composes: displayFlexSpaceBetw;
	width: 84px;
}

.svgBasket,
.svgSearch {
	cursor: pointer;
}
.groupbasket {
	position: relative;
}

.svgCount {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	left: 20px;
	bottom: 0;
	width: 16px;
	height: 16px;
	background-color: #fdfdfd;
	border-radius: 50%;
	color: #000;
}

.listBasket {
	position: fixed;
	padding: 20px;
	top: 0;
	transform: translate(110%);
	width: 400px;
	height: 100vh;
	background-color: #0f0f0f;
	color: #fdfdfd;
	z-index: 3;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 20px;
	transition: all 0.4s;
}
.svgBasketActive {
	position: fixed;
	top: 0;
	width: 400px;
	height: 100vh;
	padding: 20px;
	color: #fdfdfd;
	background-color: #0f0f0f;
	transform: translate(11%);
	z-index: 3;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	transition: all 0.4s;
}

.BasketList {
	display: flex;
	flex-direction: column;
	overflow-y: auto;
	gap: 20px;
}

.basketContent {
	display: flex;
	justify-content: space-between;
}
