@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=PT+Sans:wght@400;700&family=Playfair+Display+SC:wght@400;900&display=swap');

.regular {
	font-weight: 400;
	font-style: normal;
}
.bolds {
	font-weight: 900;
	font-style: normal;
}

.playfair-regular {
	font-family: 'Playfair Display SC', serif;
	composes: regular;
}

.playfair-Bbold {
	font-family: 'Playfair Display SC', serif;
	composes: bolds;
}

.pt-regular {
	font-family: 'PT Sans', sans-serif;
	composes: regular;
}

.pt-bold {
	font-family: 'PT Sans', sans-serif;
	composes: bolds;
}

.black-ops-regular {
	font-family: 'Black Ops One', system-ui;
	composes: regular;
}

.wrapper {
	display: flex;
	flex-direction: column;
	height: 100vh;
}

.container {
	width: 1200px;
	margin: 0 auto;
}

.basketItem {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.basketImg {
	width: 100px;
	height: 100px;
}

.basketContent {
}

.basketItemTitle {
	composes: playfair-regular;
	font-size: 18px;
}

.basketItemPrice {
	composes: playfair-regular;
	font-size: 14px;
}

.bsketBtn {
	cursor: pointer;
	background-color: transparent;
	border: none;
	outline: none;
}
