.main {
	flex-grow: 1;
}

.top {
	position: relative;
	background: url('./img/bacgroundImg.jpg') no-repeat;
	background-position: center center;
	background-size: cover;
	width: 1920px;
	height: 810px;
	margin: 0 auto;
}

.topContent {
	position: absolute;
	left: 50%;
	top: 40%;
	transform: translate(-50%, -50%);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 320px;
	height: 320px;
	border: 2px solid #fff;
}

.logoContent {
	composes: black-ops-regular from '../App.module.css';
	font-size: 64px;
	color: #fff;
	text-transform: uppercase;
	border-bottom: 1px solid #fff;
}

.subscribe {
	composes: pt-regular from '../App.module.css';
	margin-top: 10px;
	color: #fff;
	text-transform: uppercase;
	font-size: 22px;
}

.subscribeText {
	position: absolute;
	left: 50%;
	top: 60%;
	transform: translate(-50%, 100%);
	composes: pt-regular from '../App.module.css';
	width: 793px;
	font-size: 16px;
	text-align: center;
	line-height: 150%;
	text-transform: uppercase;
	color: #fff;
}

.seasonGroup {
	display: flex;
	flex-wrap: wrap;
	width: 1920px;
	margin: 0 auto;
}

.seasonBlock1 {
	width: 1040px;
	height: 730px;
	padding: 93px 80px;
	background-color: #000;
	color: #fff;
}

.seasonTitle1 {
	composes: playfair-Bbold from '../App.module.css';
	text-align: center;
	font-size: 36px;
	margin-bottom: 50px;
}

.seasonUnder1 {
	display: block;
	width: 120px;
	height: 1px;
	background-color: #fff;
	margin: 0 auto;
}

.seasonList {
	display: flex;
	justify-content: space-between;
	margin-top: 50px;
}

.itemTitle {
	composes: playfair-regular from '../App.module.css';
	font-size: 18px;
	margin: 25px 0 10px;
}

.seasonBlock2 {
	display: flex;
	flex-direction: column;
	justify-content: end;
	align-items: center;
	width: 880px;
	height: 730px;
	padding: 100px;
	background: url('./img/blockItem.jpg') no-repeat;
	background-position: center center;
	background-size: cover;
	color: #fff;
}

.seasonTitle2 {
	composes: playfair-Bbold from '../App.module.css';
	font-size: 36px;
	margin-bottom: 30px;
}

.seasonBtn {
	composes: pt-regular from '../App.module.css';
	margin-top: 30px;
	padding: 12px 28px;
	font-size: 18px;
	text-transform: uppercase;
	background-color: transparent;
	border: 3px solid #fff;
	color: #fff;
	text-shadow: 5px 3px 10px #000;
	transition: all 0.5s;

	&:hover {
		background-color: #fff;
		color: #000;
		transition: all 0.5s;
	}
}

.seasonBlock3 {
	background: url('./img/blockItem2.jpg') no-repeat;
	background-position: center center;
	background-size: cover;
	width: 880px;
	height: 600px;
}

.seasonBlock4 {
	width: 1040px;
	height: 600px;
	padding: 100px 0;
	background-color: #000;
	color: #fff;
}
.seasonBlockContent {
	width: 471px;
	margin: 0 auto;
}

.seasonTitle3 {
	composes: playfair-regular from '../App.module.css';
	font-size: 36px;
	text-align: center;
	margin-bottom: 40px;
}

.blockText {
	margin: 40px auto 61px;
	line-height: 1.5;
	text-align: center;
}

.btnBlock {
	display: block;
	composes: pt-regular from '../App.module.css';
	font-size: 18px;
	margin: 0 auto;
	padding: 12px 28px;
	text-transform: uppercase;
	color: #fff;
	background-color: transparent;
	outline: 2px solid #fff;
	transition: all 0.5s;

	&:hover {
		background-color: #fff;
		color: #000;
		transition: all 0.5s;
	}
}

.new {
	padding: 120px 0 150px;
	background-color: #000;
	color: #fff;
}

.newTitle, .brendsTitle {
	composes: playfair-Bbold from '../App.module.css';
	font-size: 36px;
	text-align: center;
	margin-bottom: 40px;
}

.newList, .brendList {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 40px 23px;
	margin-top: 81px;
}

.listItem {
	width: 260px;
}

.listItemImg {
	margin-bottom: 25px;
}

.itemTitle {
	composes: playfair-regular from '../App.module.css';
	font-size: 18px;
	margin-bottom: 10px;
}
.itemPrice {
	composes: playfair-regular from '../App.module.css';
	font-size: 16px;
	margin-bottom: 10px;
}

.itemButton {
	position: relative;
	background-color: transparent;
	padding: 8px 12px;
	border: 1px solid #fff;
	color: #fff;
	z-index: 2;
	transition: all 0.4s;

	&::after {
		position: absolute;
		left: 0;
		bottom: 0;
		content: '';
		width: 100%;
		height: 0;
		z-index: -1;
		transition: all 0.4s;
	}

	&:hover {
		color: #000;
		box-shadow: 0 5px 100px #fff;
		transition: all 0.4s;

		&::after {
			top: 0;
			height: 100%;
			background-color: rgba(255, 255, 255, 1);
			transition: all 0.4s;
		}
	}
}

.brends{
	padding: 60px 0 80px;
	background-color: #000;
	color: #fff;
}

.brendList{
	margin-top: 40px;
}
