.footer {
	background: url('./img/footerImg.jpg') no-repeat;
	background-position: center center;
	background-size: cover;
	padding: 80px 0 100px;
	background-color: #000;
	color: #fff;
}
.footerInner {
	display: flex;
	justify-content: center;
	gap: 31px;
}

.footerForShop,
.footerCategory,
.footerForm {
	width: 350px;
}

.footerTitle {
	composes: playfair-regular from '../App.module.css';
	font-size: 24px;
	margin-bottom: 40px;
}

.foooterSubtitle,
.footerListItem,
.footerFormSub,
.fromBtn {
	composes: pt-regular from '../App.module.css';
	font-size: 14px;
}

.footerList {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
}

.footerListItem {
	padding: 10px 20px;
	line-height: 1.5;
	background-color: #0f0f0f;
	transition: all 0.5s;

	&:hover {
		box-shadow: 0 0 50px rgba(255, 255, 255, 0.5);
		transition: all 0.5s;
	}
}

.footerFormSub {
	margin-bottom: 37px;
}

.formInput {
	background-color: rgba(255, 255, 255, 0.4);
	color: #fff;
	line-height: 1.5;
	padding: 10px 20px;
	border: none;

	&::placeholder {
		color: #fff;
	}
}

.fromBtn {
	position: relative;
	padding: 9.5px 20px;
	color: #fff;
	line-height: 1.5;
	background-color: #0f0f0f;
	overflow: hidden;
	border: none;
	transition: all 0.4s;

	&::after {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 0;
		height: 100%;
		background-color: rgba(255, 255, 255, 0.4);
		transition: all 0.4s;
	}

	&:hover {
		box-shadow: 0 0 100px rgba(255, 255, 255, 0.4);
		&::after {
			right: 0;
			width: 100%;
			transition: all 0.4s;
		}
	}
}

.footerCol {
  color: #d0d0d0;
	background-color: #0f0f0f;
	padding: 17px;
}
.footerColInner {
	display: flex;
	justify-content: space-between;
}

.privacy,
.created{
  composes:pt-regular from '../App.module.css';
  font-size: 12px;
}

.created{
  text-decoration:underline;
}
